export default {
	props: ["newsContent"],
	data() {
		return {
			res: {},
			flag: false,
			// 需要添加的行内样式
			height: {
				height: "auto"
			},
			widths: {
				width: 70 + "%",
				float: "right"
			},
			tops: {
				height: 65 + "%"
			},
			rest: {}
		}
	},
	methods: {
		love() {
			uniCloud.callFunction({
				name: "aaa",
				data: {
					isFlag: this.flag
				},
				success: (res) => {
					console.log(res);
					this.flag = !this.flag
					if (this.flag) {
						uni.showToast({
							title: '添加收藏',
							icon: "none",
							duration: 2000
						});
					} else {
						uni.showToast({
							title: '取消收藏',
							icon: "none",
							duration: 2000
						});
					}
				},
				fail: (err) => {
					console.log(err);
					if (this.flag) {
						uni.showToast({
							title: '取消失败',
							icon: "none",
							duration: 2000
						});
					} else {
						uni.showToast({
							title: '添加失败',
							icon: "none",
							duration: 2000
						});
					}
				}
			})
		}
	},
	created() {
		this.res = this.$props.newsContent
		this.res.see = 152
		// 判断几张图片，如果是小于三张大于零张就改变添加行内样式
		if (this.res.img.length == 1 || this.res.img.length == 2) {
			this.height.height = 120 + "px";
			this.widths = {
				width: 60 + "%",
				float: "right"
			}
		} else {
			this.height.height = "auto"
			this.widths = null;
		}
	}
}
